'use client';

import { useTranslation } from "react-i18next";
import { ThemeProvider, useTheme } from "../../context/ThemeContext";
import Sidebar from "@/components/atoms/Sidebar/Sidebar";
import CryptoCard from "@/components/molecules/Dashboard/CryptoCard";
import { useEffect, useState } from "react";
import { CryptoOhlcvStats, CombinedData } from "@/app/types";
import "../../app/global.scss";
import "../../../i18n";

export default function LoginPage({ children }: { children: React.ReactNode }) {
    return (
        <ThemeProvider>
            <Dashboard>{children}</Dashboard>
        </ThemeProvider>
    );
}

const Dashboard = ({ children }: { children: React.ReactNode }) => {
    const { theme } = useTheme();
    const { t } = useTranslation();

    const [ohlcvData, setOhlcvData] = useState<CryptoOhlcvStats | null>(null);
    const [combinedData, setCombinedData] = useState<CombinedData | null>(null);

    useEffect(() => {
        const getData = async () => {
            const [ohlcv, combined]: [CryptoOhlcvStats, CombinedData] = await Promise.all([
                fetch('/api/ohlcv?id=1').then((res) => res.json()),
                fetch('/api/combined-data?id=1').then((res) => res.json()),
            ]);
            setOhlcvData(ohlcv);
            setCombinedData(combined);
        };

        getData();
    }, []);

    return (
        <div>
            {/* <Sidebar /> */}
            {ohlcvData && combinedData && (
                <CryptoCard ohlcv={ohlcvData} combined={combinedData} />
            )}
            <main>{children}</main>
        </div>
    );
};
